import { useState } from "react"
import ReactPlayer from "react-player"
import "./Popup.css"
import Popup from "reactjs-popup"
export const Features = (props) => {
  const [changeOpen, setOpenChange] = useState(false);
  const [video_url, setVideo_url] = useState("");
  const [video_title, setVideo_title] = useState("");
  return (
    <div id='features' className='text-center'>
      <div className='container'>
        <div className='col-md-10 col-md-offset-1 section-title'>
          <h2>Data Competition</h2>
        </div>
        <div className='row'>
          {props.data
            ? props.data.map((d, i) => (
              <div key={`${d.title}-${i}`} className='col-xs-12 col-md-6'>
                {' '}
                <div class="d-flex justify-content-center" onClick={() => {
                  if (d.isVideo) {
                    setOpenChange(true);
                    setVideo_url(d.videoPath);
                    setVideo_title(d.title);
                  }
                }}>
                  <i className={d.icon}></i>
                  <h3>{d.title}</h3>
                  <p>{d.text}</p>
                </div>
              </div>
            ))
            : 'Loading...'}
          <div class="player-wrapper">
            <Popup
              open={changeOpen}
              onClose={() => setOpenChange(false)}
              modal
              nested
            >
              {close => (
                <>
                  <button id="exitbutton" onClick={close}>
                    &times;
                  </button>
                  <div className="modal2">
                    <div className="header"> {video_title} </div>
                    <div className="popup-content">
                      <ReactPlayer
                        url={video_url}
                        controls={true}
                        width="90%"
                        height="50%"
                        className='react-player fixed-bottom' />
                    </div>
                  </div>
                </>
              )}
            </Popup>
          </div>
        </div>
      </div>
    </div>
  )
}
